import React from 'react';
import Header from '../../component/header/header';
import { Button, ButtonArea }  from 'react-ysui';
import './button.scss';

const Buttons = () => (
  <div className="page">
    <Header
      title="Button"
      desc=""
    />
    <Button></Button>
    <Button selected></Button>
    <Button disabled></Button>
    <br />
    <Button type="warn"></Button>
    <Button type="warn" selected></Button>
    <Button type="warn" disabled></Button>
    <br />
    <Button type="default"></Button>
    <Button type="default" selected></Button>
    <Button type="default" disabled></Button>
    <br />
    <ButtonArea>
      <Button></Button>
      <Button></Button>
    </ButtonArea>
    <ButtonArea>
      <Button></Button>
      <Button selected className="fixWidth"></Button>
    </ButtonArea>
    <br />
  </div>
);
export default Buttons;



